.landingPage_container {
  width: 100%;
  margin: auto;
}

.landingPage_center {
  display: block;
  text-align: right;
}

.landingPage_menu {
  background-color: white;
  box-shadow: 0px 2px 24px 0px rgba(0, 0, 0, 0.15);
  border-radius: 0 0 5px 5px;
  width: 100%;
  height: 80px;
  display: flex;
  justify-content: space-between;
  padding: 0 30px;
  position: relative;
  margin-bottom: 30px;
  position: fixed;
  top: 0;
  z-index: 3;
}

.landingPage_menu__list {
  border: none;
  box-shadow: unset;
  letter-spacing: 1px;
  text-transform: uppercase;
  height: fit-content;
  background-color: white;
  border-radius: 5px;
  padding: 10px 10px;
  margin: 15px 10px;
  font-size: 30px;
  font-weight: 600;
  color: black;
  /* margin-right: 10px; */
}
.landingPage_menu__list:hover {
  /* background-color: rgb(252, 102, 102); */
  color: black;
  font-weight: 600;
}
.landingPage_menu__logo {
  display: flex;
  height: 100%;
  width: 92px;
  align-items: center;
  background-size: cover 100%;
  background-repeat: no-repeat;
  background-position: center;
  margin: 0;
}

/*hamburger*/
.hamBtn {
  background-color: white;
  border: hidden;
  align-self: center;
  height: fit-content;
}

.hamBtn:focus {
  outline: transparent;
}

.hamburger {
  display: inline-block;
  cursor: pointer;
  transition-property: opacity, -webkit-filter;
  transition-property: opacity, filter;
  transition-property: opacity, filter, -webkit-filter;
  transition-duration: 0.15s;
  transition-timing-function: linear;
  font: inherit;
  color: black;
  text-transform: none;
  background-color: transparent;
  border: 0;
  margin: 0;
  overflow: visible;
}

.hamburger:hover {
  opacity: 0.7;
}

.hamburger-box {
  width: 30px;
  height: 30px;
  display: inline-block;
  position: relative;
}

.hamburger-inner {
  display: block;
  top: 50%;
  margin-top: 2px;
}

.hamburger-inner,
.hamburger-inner::before,
.hamburger-inner::after {
  width: 30px;
  height: 3px;
  background-color: black;
  border: none;
  position: absolute;
  transition-property: -webkit-transform;
  transition-property: transform;
  transition-property: transform, -webkit-transform;
  transition-duration: 0.15s;
  transition-timing-function: ease;
}

.hamburger-inner::before,
.hamburger-inner::after {
  content: "";
  display: block;
}

.hamburger-inner::before {
  top: -10px;
}

.hamburger-inner::after {
  bottom: -10px;
}

.hamburger-spin .hamburger-inner {
  transition-duration: 0.3s;
  transition-timing-function: cubic-bezier(0.55, 0.055, 0.675, 0.19);
}

.hamburger-spin .hamburger-inner::before {
  transition: top 0.1s 0.34s ease-in, opacity 0.1s ease-in;
}

.hamburger-spin .hamburger-inner::after {
  transition: bottom 0.1s 0.34s ease-in,
    -webkit-transform 0.3s cubic-bezier(0.55, 0.055, 0.675, 0.19);
  transition: bottom 0.1s 0.34s ease-in,
    transform 0.3s cubic-bezier(0.55, 0.055, 0.675, 0.19);
  transition: bottom 0.1s 0.34s ease-in,
    transform 0.3s cubic-bezier(0.55, 0.055, 0.675, 0.19),
    -webkit-transform 0.3s cubic-bezier(0.55, 0.055, 0.675, 0.19);
}

.hamburger-spin.is-active .hamburger-inner {
  -webkit-transform: rotate(225deg);
  transform: rotate(225deg);
  transition-delay: 0.14s;
  transition-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
}

.hamburger-spin.is-active .hamburger-inner::before {
  top: 0;
  opacity: 0;
  transition: top 0.1s ease-out, opacity 0.1s 0.14s ease-out;
}

.hamburger-spin.is-active .hamburger-inner::after {
  bottom: 0;
  -webkit-transform: rotate(-90deg);
  transform: rotate(-90deg);
  transition: bottom 0.1s ease-out,
    -webkit-transform 0.3s 0.14s cubic-bezier(0.215, 0.61, 0.355, 1);
  transition: bottom 0.1s ease-out,
    transform 0.3s 0.14s cubic-bezier(0.215, 0.61, 0.355, 1);
  transition: bottom 0.1s ease-out,
    transform 0.3s 0.14s cubic-bezier(0.215, 0.61, 0.355, 1),
    -webkit-transform 0.3s 0.14s cubic-bezier(0.215, 0.61, 0.355, 1);
}
.arrowDropdown {
  cursor: pointer;
  width: 160px;
  background-color: white;
  position: absolute;
  z-index: 999;
  box-shadow: 0px 2px 24px 0px rgba(0, 0, 0, 0.15);
  font-size: 16px;
  font-weight: 500;
  color: rgb(77, 76, 76);
  letter-spacing: 1px;
  text-transform: uppercase;
  right: 0px;
}
.arrowDropdown div {
  padding: 1rem 1rem;
}
.arrowDropdown div:hover {
  background-color: lightgray;
}
